import { Button } from "@webs/component-library/button";

import { addCodeExample } from "./helpers/code-example";

export default {
  title: "Example/Button",
  argTypes: {
    button_color: {
      control: {
        type: "select",
        options: ["", "violet", "pink"],
      },
    },
    title: {
      control: {
        type: "select",
        options: ["Short", "Medium Length", "Rather Long Button Text"],
      },
    },
  },
};

const Template = (module) => {
  return `
    <webs-button
      text=${module.title}
      extraClasses="button-${module.button_color}"
      href=""
      openInNewTab="true"
    ></webs-button>
  `;
};

export const Primary = Template.bind({
  hoverColor: "",
  text: "Short",
});

